import React, { Component } from 'react';
import Product from './Product';
import _ from 'lodash';

class Cart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      shipmentMethods: [],
      sum: 0,
      currency: "USD",
      showModal: false,
      selectedProduct: {},
      selectedShipmentMethod: this.props.contaktiChat.shipment_methods[0],
      selectedCheckoutMethod: this.props.contaktiChat.checkout_methods[0]
    }
  }
  componentDidMount() {
    this.countSum();
    let { contaktiChat } = this.props;
  }

  onProductRemove = (product) => {
    let { products } = this.state;
    products = _.reject(products, (p) => {
      return p.id === product.id;
    });
    this.setState({ products }, () => {
      if (this.state.products.length === 0) {
        this.props.onCartEmpty();
      }
      this.countSum();
    });

  }

  addProduct = (p) => {
    let { products } = this.state;
    var product = _.find(products, { id: p.id });
    if (product) {
      product.quantity += 1;
      this.replaceProduct(product);
    }
    else {
      p.quantity = 1;
      let currency = p.currency;
      products.push(p)
      this.setState({ products, currency }, () => this.countSum());

    }
  }


  countSum = () => {
    let sum = 0;
    let { products } = this.state;
    _.each(products, (product) => {
      sum += (product.price * product.quantity);
    });
    this.setState({ sum });
  }

  replaceProduct = (product) => {
    let { products } = this.state;
    var index = _.findIndex(products, { id: product.id });
    products.splice(index, 1, product);
    this.setState({ products });
    this.countSum();
  }

  handleShipmentChange = (event) => {
    this.setState({selectedShipmentMethod: event.target.value });
  }

  handleCheckoutMethodChange = (event) => {
    this.setState({selectedCheckoutMethod: event.target.value });
  }

  render() {
    let { products, selectedCheckoutMethod } = this.state;
    const { contaktiChat } = this.props
    let color = contaktiChat.color;
    let text_color = contaktiChat.text_color;
    const { translations } = contaktiChat
    return (
      <div id="contakti_cart_box" className="cart_box">
        <div className="msg_head" style={{ backgroundColor: color }}>
          <span id="contakti-user-name" style={{ color: text_color }}> {translations.user_dashboard.shopping_cart.cart_title}</span>
          <span id="close-cart-icon" className="float-end p-1 mr-2 d-none d-sm-block d-md-none" onClick={this.props.toggleCart}><i className="fa fa-times"></i></span>
        </div>

        <div className="cart_body d-flex flex-column justify-content-between">
          <div style={{ maxHeight: "270px", overflow: "auto" }}>
            <Product
              products={products}
              onProductRemove={this.onProductRemove}
              increaseQuantity={this.addProduct}
              replaceProduct={this.replaceProduct}
              total={`${this.state.currency} ${this.state.sum}`}
              translations={translations.user_dashboard.shopping_cart}
              handleShow={this.props.handleShow}
            />
          </div>
          <div className="text-center">
            <div className="row form-group">
              <div className="col-6">
                <label> {translations.user_dashboard.shopping_cart.checkout_method}</label>
                <div className="centera" id="checkout_method_select">
                  <select className="form-control w-100 m-auto" onChange={this.handleCheckoutMethodChange}
                          value={this.state.selectedCheckoutMethod}>
                    {contaktiChat.checkout_methods.map((method, index) => {
                      return <option value={method}> {eval("translations.service_channels." + method )}</option>
                    })}
                  </select>
                </div>
              </div>

              <div className="col-6">
              <label> {translations.shipment_methods.title}</label>
                <div className="centera" id="shipment_method_select">
                  <select className="form-control w-100 m-auto" onChange={this.handleShipmentChange}
                          value={this.state.selectedShipmentMethod}>
                    {contaktiChat.shipment_methods.map((method, index) => {
                      return (<option key={index}> {method.name}</option>)
                    })}
                  </select>
                </div>
              </div>
            </div>

            <div>
              {selectedCheckoutMethod === "checkout_paytrail" ? (

                <button className="btn w-50" style={{
                  color: text_color,
                  backgroundColor: color
                }}> {translations.user_dashboard.shopping_cart.checkout}</button>
              ) : (
                <button className="btn w-50" style={{
                  color: text_color,
                  backgroundColor: color
                }}
                  onClick={() => this.props.handleOrder(this.state.products, this.state.selectedCheckoutMethod, this.state.selectedShipmentMethod, this.state.sum)}
                > {translations.user_dashboard.shopping_cart.order}</button>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Cart;
